@import "../../assets//main.scss";

.barSoft {
  display: flex;
  flex-direction: column;

  &__title {
    position: relative;
    @include title($accentGreen);
    margin: 0;
    margin-bottom: 20px;
    padding-bottom: 10px;

    &::after {
      content: " ";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $textColor;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    @include text($textColor);
    margin-bottom: 5px;
  }

  &__item:last-child {
    margin-bottom: 0;
  }

  &__icon {
    width: 9px;
    height: 9px;
    margin-right: 5px;
  }
}
